import React, { Component } from 'react';
import { Table } from 'antd';
import { setStudentStudyAndExamAgain } from 'dump/data';

class TableStudentStudyAndExamAgain extends Component {
  constructor(props) {
    super(props);
    this.state = {};

    this.columns = [
      {
        title: 'M sinh vin',
        dataIndex: 'code',
        key: 'code',
      },
      {
        title: 'H v tn',
        dataIndex: 'name',
        key: 'name',
      },
      {
        title: 'Ngy sinh',
        dataIndex: 'dayOfBirth',
        key: 'dayOfBirth',
      },
      {
        title: 'Tn lp',
        dataIndex: 'class',
        key: 'class',
      },
      {
        title: 'K hiu',
        dataIndex: 'codeCourseName',
        key: 'codeCourseName',
      },
      {
        title: 'Tn hc phn',
        dataIndex: 'courseName',
        key: 'courseName',
      },
      {
        title: 'S tn ch',
        dataIndex: 'numberOfCredits',
        key: 'numberOfCredits',
      },
      {
        title: 'Hc k',
        dataIndex: 'semester',
        key: 'semester',
      },
      {
        title: 'Nm hc',
        dataIndex: 'schoolYear',
        key: 'schoolYear',
      },
      {
        title: 'Ln thi',
        dataIndex: 'timesExam',
        key: 'timesExam',
      },
      {
        title: 'im thi ln 1',
        dataIndex: 'scoresExamFirst',
        key: 'scoresExamFirst',
      },
      {
        title: 'im thi cao nht',
        dataIndex: 'highestScores',
        key: 'highestScores',
      },
    ];

    this.rowSelection = {
      onChange: (selectedRowKeys, selectedRows) => {
        console.log(`selectedRowKeys: ${selectedRowKeys}`, 'selectedRows: ', selectedRows);
      },
      getCheckboxProps: record => ({
        disabled: record.name === 'Disabled User', // Column configuration not to be checked
        name: record.name,
      }),
    };
  }

  render() {
    return (
      <div>
        <Table
          bordered
          rowSelection={this.rowSelection}
          dataSource={setStudentStudyAndExamAgain}
          columns={this.columns}
        />
      </div>
    );
  }
}

export default TableStudentStudyAndExamAgain;



